import { Component, OnInit } from '@angular/core';
import { FileChooser } from '@ionic-native/file-chooser/ngx';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Estimates } from '../models/estimates';
import { NavParams } from '@ionic/angular';
import { LocalstorageService } from '../services/localstorage.service';
import { EstimatesService } from '../services/estimates.service';
import { CompanyNavigation } from '../models/company-navigation';

@Component({
  selector: 'app-estimatemodal',
  templateUrl: './estimatemodal.component.html',
  styleUrls: ['./estimatemodal.component.scss'],
})
export class EstimatemodalComponent implements OnInit {

  objEstimate = {} as Estimates;
  objCompanyNav = {} as CompanyNavigation;
  estimateAdd: FormGroup;
  fileName : string;

  idCompEstimate: any;
  idUserCompany: any;
  userToken : any;

  constructor(private fileChooser: FileChooser, public formBuilder: FormBuilder, 
    private navparams: NavParams, private localstorage: LocalstorageService, 
    private estimateservice: EstimatesService) { }

  ngOnInit() {
    this.estimateAdd = new FormGroup({
      nameText : new FormControl(),//'', Validators.compose([Validators.required, CompanyValidator.isValid])),
      cel : new FormControl(),
      email: new FormControl(),
      state: new FormControl ()
    });
    this.idCompEstimate = this.navparams.get('idCompanyEstimate');
    console.log('pos ==> '+this.idCompEstimate);
    this.getData();
    
  }

  async getData(){
    this.idUserCompany = await this.localstorage.getValue('compId'); 
    this.userToken     = await this.localstorage.getValue('token');
  }

  openFile(){
    this.fileChooser.open()
    .then(uri => this.fileName = uri)
    .catch(e => console.log(e));
  }

  submitForm(){
    console.log('id da emp =>'+this.idUserCompany);
    this.objEstimate.company = this.idUserCompany;
    this.objEstimate.phoneNumber = this.estimateAdd.get('cel').value;
    this.objEstimate.state = this.estimateAdd.get('state').value;
    this.objEstimate.email = this.estimateAdd.get('email').value;
    this.objCompanyNav.idCompany = this.idCompEstimate;
    this.objEstimate.companyNavigation = this.objCompanyNav;
    console.log(this.objEstimate);
    this.estimateservice.addEstimated(this.objEstimate, this.userToken).subscribe((ret)=>{
      console.log(ret);
    });
    
    /*this.postFeed.productValue = this.postPhotoSel.get('postValue').value;
    let date = new Date().toUTCString();
    //this.postFeed.postDate = formatDate()  date;

    var re = "data:image/jpeg;base64,"; 
    this.postFeed.media = this.croppedImagepath.replace(re,"");

    this.postfeedservice.addPrePost(this.postFeed, this.token).subscribe((ret)=>{
      console.log(ret);
    });
    //console.log('Pelo menos clicou');*/
  }

}
