import { Component, OnInit } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { Postfeed } from '../models/postfeed';
import { PostfeedService } from '../services/postfeed.service';
import { element } from 'protractor';
import { LocalstorageService } from '../services/localstorage.service';
import { Storage } from '@ionic/storage';
import { PopoverController } from '@ionic/angular';
import { PopovercomponentPage } from '../popovercomponent/popovercomponent.page'; 
import { ModalController } from '@ionic/angular';
import { EstimatemodalComponent } from '../estimatemodal/estimatemodal.component';


@Component({
  selector: 'app-feeds',
  templateUrl: './feeds.page.html',
  styleUrls: ['./feeds.page.scss'],
})
export class FeedsPage implements OnInit {

  postfeed = {} as Postfeed;
  apostfeed : Postfeed[];
  token:any;
  
  photo :string = 'data:image/jpeg;base64,';


  constructor(public atrCtrl: AlertController, private postfeedservice: PostfeedService,
    private localstorageservice:LocalstorageService, private storage: Storage,
    private popovercontroler : PopoverController, private modalCtrl: ModalController) { }

  getLimitedPosts(initial:number, maximum:number, token:any){
    console.log("token passado "+token);
    return this.postfeedservice.getPagPost(initial, maximum, token)
    .subscribe((pfeed: Postfeed[]) => {
      this.apostfeed = pfeed;
      //console.log('esse é o pfeed');      
    });
  }

  preparePhoto(){
    this.apostfeed.forEach((value)=>{
      console.log(value.postText);
    });
  }

  getValue(key:string):any{
    this.storage.get(key).then((val) => {
      console.log('get blu'+key+' ,'+val);
      this.token = val;
    })
    .catch((err)=>{
      console.log('get error for '+key+' '+err);
    });
  }

  ngOnInit() {
    this.getValue("token");
    console.log("Email da empresa");
    console.log(this.localstorageservice.getValue("companyEmail"));
  }

  ShowPopover(prodDescript:string, prodValue: number)
   {
     this.popovercontroler.create({
      component:PopovercomponentPage,
      componentProps:{
        itemDescription:prodDescript,
        itemValue: prodValue
       },
      showBackdrop:false}).then((popoverElement)=>{
       popoverElement.present();
     })
   }

  ngAfterViewInit() {
    console.log("afterinit");
    setTimeout(() => {
     
     this.getLimitedPosts(1,20, this.token); 
     //this.preparePhoto();
     // this.getAllCities();
    }, 1000);
  }

  async openEstimateModal(idPostCompany:number){
    const modal = await this.modalCtrl.create({
      component: EstimatemodalComponent,
      componentProps:{
        idCompanyEstimate : idPostCompany
      }
    });

    await modal.present();
  }

}
